@import "./App.css";

@font-face {
  font-family: 'Arkiv';
  src: url('fonts/Arkiv-Regular.eot');
  src: url('fonts/Arkiv-Regular.eot?') format('embedded-opentype'), url('fonts/Arkiv-Regular.woff') format('woff'), url('fonts/Arkiv-Regular.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Arkiv';
  src: url('fonts/Arkiv-Light.eot');
  src: url('fonts/Arkiv-Light.eot?') format('embedded-opentype'), url('fonts/Arkiv-Light.woff') format('woff'), url('fonts/Arkiv-Light.woff2') format('woff2');
  font-weight: 100;
  font-style: normal;
}

@font-face {
  font-family: 'Arkiv';
  src: url('fonts/Arkiv-Bold.eot');
  src: url('fonts/Arkiv-Bold.eot?') format('embedded-opentype'), url('fonts/Arkiv-Bold.woff') format('woff'), url('fonts/Arkiv-Bold.woff2') format('woff2');
  font-weight: bold;
  font-style: normal;
}

::selection {
  background: #DDB9EB;
}

::-moz-selection {
  background: #DDB9EB;
}

body {
  font-family: 'Arkiv';
  color: #4d4d4d;
  position: relative;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
